.sidebar-title {
    font-size: 22px;
    font-weight: normal;
    margin-bottom: 10px;
}

.logo-icon {
    font-size: 3rem;
}

.sidebar {
    width: 15%;
    position: fixed;
    top: 0;
    bottom: 0;
    border-right: 2px solid #e5e5e5;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo-container {
   margin-bottom: 3rem; 
}

.logo-container h1 {
    margin-top: 0.5rem;
}